import { Card, CardHeader, CardContent } from "./Card";
import { clients, ClientInfo } from "@/lib/models/client";
import { current } from "@reduxjs/toolkit";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import { Popup } from "./popup";

export default function ClientCard({ id }: { id: number }) {
  const router = useRouter();
  const [currentClient, setCurrentClient] = useState<ClientInfo | null>(null);
  const [isclientUpdated, setIsClientUpdated] = useState(false);

  useEffect(() => {
    const getClientById = (id: number) => {
      return clients.find((client) => client.clientId === id);
    };

    const client = getClientById(id);
    setCurrentClient(client as ClientInfo);
  });

  return (
    <Card className="p-4">
      <CardHeader>
        <h1>{currentClient?.clientName}</h1>
      </CardHeader>
      <CardContent className="bg-white p-4 mt-4">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div>
            <p className="font-semibold">Client HO</p>
            <p className="text-gray-500">{currentClient?.clientHo}</p>
          </div>
          <div>
            <p className="font-semibold">Finance POC</p>
            <p className="text-gray-500">{currentClient?.financePocName}</p>
          </div>
          <div>
            <p className="font-semibold">Finance Number</p>
            <p className="text-gray-500">{currentClient?.financeNumber}</p>
          </div>
          <div>
            <p className="font-semibold">Finance Email</p>
            <p className="text-gray-500">{currentClient?.financeEmail}</p>
          </div>
          <div>
            <p className="font-semibold">GST</p>
            <p className="text-gray-500">{currentClient?.GST}</p>
          </div>
          <div>
            <p className="font-semibold">CIN</p>
            <p className="text-gray-500">{currentClient?.CIN}</p>
          </div>
          <div>
            <p className="font-semibold">PAN</p>
            <p className="text-gray-500">{currentClient?.PAN}</p>
          </div>
        </div>
        {isclientUpdated && (
          <Popup onClose={() => setIsClientUpdated(false)}>
            <div className="bg-white mx-auto max-w-3xl p-4 mt-16 rounded-md">
              <form className="space-y-4">
                <div>
                  <label className="font-semibold">Client HO</label>
                  <input
                    type="text"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.clientHo}
                  />
                </div>
                <div>
                  <label className="font-semibold">Finance POC</label>
                  <input
                    type="text"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.financePocName}
                  />
                </div>
                <div>
                  <label className="font-semibold">Finance Number</label>
                  <input
                    type="text"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.financeNumber}
                  />
                </div>
                <div>
                  <label className="font-semibold">Finance Email</label>
                  <input
                    type="email"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.financeEmail}
                  />
                </div>
                <div>
                  <label className="font-semibold">GST</label>
                  <input
                    type="text"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.GST}
                  />
                </div>
                <div>
                  <label className="font-semibold">CIN</label>
                  <input
                    type="text"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.CIN}
                  />
                </div>
                <div>
                  <label className="font-semibold">PAN</label>
                  <input
                    type="text"
                    className="w-full p-2 border border-gray-300 rounded-md"
                    value={currentClient?.PAN}
                  />
                </div>
                <button
                  type="submit"
                  className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-md"
                >
                  Update
                </button>
              </form>
            </div>
          </Popup>
        )}
        <div className="flex justify-end">
          <button className="bg-[var(--theme-background)] text-white py-2 px-4 rounded-lg" onClick={() => setIsClientUpdated(true)}>
            Update
          </button>
        </div>
      </CardContent>
    </Card>
  );
}
